import { _decorator, Component, Node, Color } from 'cc';
import { ScaleFactor, scaleTo, TimeFactor } from '../transiton/Transformation';
const { ccclass, property } = _decorator;

@ccclass('ex_Level1')
export class ex_Level1 extends Component {
    @property({type: Node})
    answer: Node = null;

    onClickInstruction() {
        this.answer.getComponent("Sprite").color = new Color(255, 255, 255); 
        scaleTo(this.answer, ScaleFactor.BIG_BY_2, ScaleFactor.SMALL_BY_0_5, TimeFactor.MEDIUM);
    }
    start() {

    }

    update(deltaTime: number) {
        
    }
}


